import React from "react";

const Services = ({ classicHeader, darkTheme }) => {
  // services details
  const services = [
    {
      name: "User Manuals and Guides",
      desc: "Simplifying software use with comprehensive guides, tutorials, and conceptual explanations for seamless user adoption.",
      icon: "fa-solid fa-book",
    },
    {
      name: "API Documentation",
      desc: "Crafting clear and concise resources, including API references and detailed guides, to empower developers and enhance integration efforts.",
      icon: "fa-solid fa-code",
    },
    {
      name: "Online Knowledgebase",
      desc: "Building intuitive documentation portals with modern technologies like Jekyll and Docusaurus to centralize information and support user self-service.",
      icon: "fas fa-desktop",
    },
    {
      name: "Interactive Content",
      desc: "Elevating user engagement through dynamic content solutions, including animations, interactive procedures, and live code samples for enriched learning experiences.",
      icon: "fa-solid fa-computer-mouse",
    },
    {
      name: "Training Videos",
      desc: "Enhancing brand professionalism and accessibility with expertly edited micro-learning videos tailored to diverse learning preferences and styles.",
      icon: "fa-solid fa-video",
    },
    {
      name: "In-App UX Writing",
      desc: "Optimizing user interfaces with expertly crafted error messages and UI text to improve user understanding and interaction within applications.",
      icon: "fa-solid fa-pen-ruler",
    },
  ];

  return (
    <section
      id="services"
      className={"section " + (darkTheme ? "bg-dark-2" : "bg-light")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-white-50  opacity-1" : "text-light  opacity-4")
            }
          >
            Services
          </h2>
          <p
            className={
              "text-9  fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            What I do
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        {/* content start */}
        <div className="row">
          <div className="col-lg-11 mx-auto">
            <div className="row">
              {services.length > 0 &&
                services.map((service, index) => (
                  <div className="col-md-6" key={index}>
                    <div className="featured-box style-3 mb-5">
                      <div
                        className={
                          "featured-box-icon text-primary  shadow-sm rounded " +
                          (darkTheme ? "bg-dark-1" : "bg-white")
                        }
                      >
                        <i className={service.icon} />
                      </div>
                      <h3 className={darkTheme ? "text-white" : ""}>
                        {service.name}
                      </h3>
                      <p
                        className={"mb-0 " + (darkTheme ? "text-white-50" : "")}
                      >
                        {service.desc}
                      </p>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
        {/* content end */}
      </div>
    </section>
  );
};

export default Services;
